import React, { Component } from 'react'
import { Container, Row, Col } from 'react-bootstrap'
import CartHeader from './CartHeader'
import CartItemProductArea from './CartItemProductArea'
import CartItemHotel from './CartItemHotel'
import CartToCheckout from './CartToCheckout'

import 'bootstrap/dist/css/bootstrap.css'
import '../../../css/cart/shopping_cart.css'
import '../../../css/header.css'

class UserCart extends Component {
  constructor() {
    super()
    this.state = {
      cartResponse: '',
    }
  }
  componentDidMount() {
    const url = 'http://localhost:3001/api/cart'
    fetch(url, {
      headers: {
        'content-type': 'application/json',
      },
    })
      .then(response => {
        return response.json()
      })
      .then(json => {
        console.log(json)
        this.setState({ carResponse: json })
      })
      .catch(err => {
        console.log(err)
      })
  }
  render() {
    let productAreaItem = '',
      hotelItem = ''
    if (this.state.cartResponse !== '') {
      let { item } = this.state.carResponse
      console.log(item)
      productAreaItem =
        item.prodType === 'products' || 'areas' ? <CartItemProductArea /> : ''
      hotelItem = item.prodType === 'hotels' ? <CartItemHotel /> : ''
    }
    return (
      <>
        <Container>
          <CartHeader />
          <Row className="align-items-start mt-4">
            <Col md={1}></Col>
            <Col md={7}>
              {productAreaItem}
              {hotelItem}
            </Col>
            <Col md={3} className="shop-order p-0 mt-4">
              <CartToCheckout />
            </Col>
            <Col md={1}></Col>
          </Row>
        </Container>
      </>
    )
  }
}

export default UserCart
